.LogicVentana{
   width:50vw;
   height:50vh;
   position:absolute;
   top:30%;
   left:30%;
   z-index:4;
   display: flex;
   flex-direction:column;
   min-width:800px;
}

.LogicVentana > .header{
    display:flex;
    height:40px;
    background-color: var(--color-escala-gris-1);
    flex-direction:row-reverse;
    border-top: 2px solid var(--color-escala-gris-4);
    border-left: 1px solid var(--color-escala-gris-3);
    border-right: 1px solid var(--color-escala-gris-3);
}

.LogicVentana > .header > button{
    width:40px;
    height:40px;
    color:var(--color-escala-gris-4);
    border:none;
    background-color:var(--color-escala-gris-1);
}

.LogicVentana > .header > button:hover{
    background-color:rgb(25, 25, 25);
}

.LogicVentana > .content {
    display:flex;
    background-color:var(--color-escala-gris-2);
    flex-direction:column;
    flex-grow:3;
    opacity:0.8;
}

@media only screen and (max-width: 1200px){
    .LogicVentana{
        width:100vw !important;
        height:100vh !important;
        min-width:0;
        min-height:0;
        top:0;
        left:0;
    }
    
}
